@import '../../style/App.scss';

#about {
  padding: 60px 0;
  color: white;
}
p{
  text-align: center;
}

.download_cv_text{
  margin-top: 1%;
  display: flex;
  text-align: left;
}

.resume_dow_btn {
    width: min-content;
  height: min-content;
  margin-top: 2%;
  background-color: $main-color;
  border: 2px solid $main-color;
  padding: 15px 30px;
  cursor: pointer;
  border-radius: 10px;
  
  a{
    font-size: 1.4rem;
    font-weight: 800;
    color: black;
  }
}
.resume_dow_btn:hover {
  border: 2px solid $main-color;
  background-color: transparent;
    a{
      color: white;
    }
}

