.about_box_li{
  margin-top: 5%;
}
.about_box_icon{
  display: flex;
  color: white;
  font-size: 25px;
  font-weight: bold;
  float: left;
}

.about_box_title{
  display: inline-flex;
  // margin: auto;
  color: $main-color;
  font-size: 18px;
  font-weight: bold;
}

.about_box_value{
  display: inline-flex;
  width: max-content;
  // margin: auto;
  font-size: 16px;
}


.other-intrest-sec{
  padding: 0% 5%;
  margin: 7% 0%;
}

@media screen and (max-width: 500px) {
  #about {
    text-align: center;
  }
  .resume_dow_btn{
    margin: auto;
    margin-top: 5%;
  }
  .download_cv_text{
    justify-content: center;
    text-align: center;
  }
  .about_box_title{
    text-align: center;
    // position: fixed;
  }
  .about_box_value{
    text-align: center;
    // position: fixed;
  }
}